* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.page {
  min-height: 100vh;
}
.page .content-aria {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: width 0.3s;
  float: right;
  background-color: #f2f1f6;
}
.page .content-aria.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #292c33;
  color: #fff;
  padding: 1rem;
  transition: left 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}
.page .sidebar .logo-area {
  font-weight: bold;
  font-size: 1.625rem;
  margin-bottom: 1rem;
}
.page .sidebar .links-area li a {
  display: block;
  position: relative;
  color: #fff;
  padding: 0.625rem;
}
.page .sidebar .links-area > li > a {
  position: relative;
  padding-left: 20px;
}
.page .sidebar .links-area > li > a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 2px solid #fff;
  transform: translateY(-50%);
}

.header {
  background-color: #fff;
  padding: 1rem;
}
.header .toggle-sidebar {
  cursor: pointer;
}